import logo from "./logo.svg";
import "./App.css";
import Button from "@mui/material/Button";
import SimpleDialog from "./components/SimpleDialog";
import Filter from "./components/Filter";
import Table from "./components/Table";
import axios from "axios";
import Select from "@mui/material/Select";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import { useEffect, useState } from "react";

function App(props) {
  const [d, set_d] = useState([]);
  const [hasFilter, setHasFilter] = useState(false);
  const [filters, setFilters] = useState({});
  const [page, setPage] = useState(1);
  const [lastItemCreatedAt, setLastItemCreatedAt] = useState(null);
  const [open, setOpen] = useState(false);
  const [list, setList] = useState([]);
  const [dropdown_sensors, set_dropdown_sensors] = useState([]);
  const [dropdown_types, set_dropdown_types] = useState([]);
  const [disableDialogActions, setDisableDialogActions] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = (value) => {
    setOpen(false);
  };

  let types = [
    {
      name: "Starbucks",
      url: "https://versa-tool-mqtt-dev-svc.azurewebsites.net", // "https://versa-tool-mqtt-dev-svc.azurewebsites.net",
      type: "Starbucks",
    },
    {
      name: "Pandora",
      url: "https://versa-tool-mqtt-dev-svc.azurewebsites.net", // "https://versa-tool-mqtt-dev-svc.azurewebsites.net",
      type: "Pandora",
    },
  ];

  const [env, setEnv] = useState(types[0]);

  useEffect(() => {
    console.log(process.env);
    axios
      .get(`${env.url}/detections/1`, { params: { type: env.type } })
      .then((res) => {
        if (res.data.detections.length > 1) {
          // setLastItemCreatedAt(moment(res.data.detections[0].createdAt).subtract(8, 'hours'));
          setLastItemCreatedAt(res.data.detections[0].timestamp_str);
        }
        set_d(res.data.detections);
      })
      .catch((err) => {
        console.log("err", err);
      });

    axios
      .get(`${env.url}/sensors/list`)
      .then((res) => {
        setList(res.data.mqtt_sensors);
        set_dropdown_sensors(res.data.sensors);
        set_dropdown_types(res.data.types);
      })
      .catch((err) => {
        console.log("err", err);
      });
  }, [env]);

  const goFilter = (filters) => {
    setFilters({
      ...filters,
      type: env.type,
    });

    axios
      .get(`${env.url}/detections_filter/1`, { params: { ...filters } })
      .then((res) => {
        if (res.data.detections.length > 1) {
          setLastItemCreatedAt(res.data.detections[0].timestamp_str);
        }
        set_d(res.data.detections);
        setHasFilter(true);

        setTimeout(() => {
          if (res.data.detections.length === 0) {
            alert("Ooops! :P No Result");
          }
        }, 100);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };
  // https://viana-internal-snapper-staging-svc.azurewebsites.net
  const loadMore = (prev) => {
    if (hasFilter) {
      axios
        .get(`${env.url}/detections_filter/${page + 1}`, {
          params: { ...filters },
          lastItemCreatedAt,
        })
        .then((res) => {
          setPage(page + 1);
          set_d([...prev, ...res.data.detections]);
        })
        .catch((err) => {
          console.log("err", err);
        });
    } else {
      axios
        .get(`${env.url}/detections/${page + 1}`, {
          params: { lastItemCreatedAt },
        })
        .then((res) => {
          setPage(page + 1);
          set_d([...prev, ...res.data.detections]);
        })
        .catch((err) => {
          console.log("err", err);
        });
    }
  };

  const addSensor = (item) => {
    setDisableDialogActions(true);
    axios
      .post(`${env.url}/sensors/update`, { ...item })
      .then((res) => {
        setDisableDialogActions(false);
        set_dropdown_sensors(res.data.sensors);
        setList(res.data.newList);
      })
      .catch((err) => {
        setDisableDialogActions(false);
        alert("There was an error while adding your sensor");
        console.log("err", err);
      });
  };

  const removeSensor = (index) => {
    setDisableDialogActions(true);
    axios
    .post(`${env.url}/sensors/delete`, { serial_id: list[index].serial_id })
    .then((res) => {
      setDisableDialogActions(false);
      set_dropdown_sensors(res.data.sensors);
      setList((list) => {
        let copy = [...list];
        copy.splice(index, 1);
        return copy;
      })
    })
    .catch((err) => {
      setDisableDialogActions(false);
      alert("There was an error while removing your sensor");
      console.log("err", err);
    });
  }

  return (
    <div>
      <link
        rel="stylesheet"
        href="https://fonts.googleapis.com/icon?family=Material+Icons"
      />
      <body>
        <div>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <Filter
              switches={() => {
                props.switches();
              }}
              goFilter={(d) => goFilter(d)}
            />
            <div style={{ display: "flex" }}>
              <FormControl sx={{ m: 1, minWidth: 120 }}>
                <InputLabel id="demo-simple-select-autowidth-label">
                  TYPE
                </InputLabel>
                <Select
                  labelId="demo-simple-select-autowidth-label"
                  id="demo-simple-select-autowidth"
                  value={env.name}
                  onChange={(e) =>
                    setEnv(types.find((d) => d.name === e.target.value))
                  }
                  autoWidth
                  label="TYPE"
                >
                  {types.map((ed) => (
                    <MenuItem value={ed.name}>{ed.name}</MenuItem>
                  ))}
                </Select>
              </FormControl>

              <SimpleDialog
                env={env}
                updateList={(index) => removeSensor(index)}
                disableDialogActions={disableDialogActions}
                onAdd={(item) => addSensor(item)}
                open={open}
                onClose={handleClose}
                data={list}
                availableSensors={dropdown_sensors}
                availableTypes={dropdown_types}
              />
              <Button variant="text" onClick={handleClickOpen}>
                config
              </Button>
            </div>
          </div>
          <Table env={env} detectionz={d} loadMore={(prev) => loadMore(prev)} />
        </div>
      </body>
    </div>
  );
}

export default App;
